.form {
	border-bottom: 1px dashed #ccc;
	margin: 0 0 1.2em;
}
	
/* form sections */

.form header {
	padding: 1em 5%;
	margin: auto;
}

.form section,.form p {
	background: #eee;
	padding: 1em 5%;
	border-top: 1px dashed #ccc;
	margin: auto;
}

/* form typography */

.form h1 {
	color: #ff0000af;
}
	
.form h2 {
	font-size: 1.2em;
	color: rgba(48, 62, 253, 0.582);
	margin: 0 0 .6em;
}

/* form fields */

.form input {
	display: inline-block;
}

.form input,
.form textarea{
	border: 1px solid #ccc;
}

.form input,
.form select,
.form textarea{
	box-sizing: border-box;
	padding: .5em;
	margin: .25em .125em;
}
	
.form input {
	width: 100%;
	max-width: 18em;
}

#state,
#zip {
	max-width: 6em;
}
	
.form textarea {
	width: 100%;
	max-width: 40em;
	line-height: 1.4;
}

/* form button */

#previewBtn {
	display: inline-block;
	width: 13.5em;
	color: rgb(255, 255, 255);
	background: #71f6ff;
	padding: 1.2em;
	border: 5px solid rgb(252, 153, 153);
	border-radius: .5em;
	margin: .25em .125em;
}

#previewBtn:active, 
#previewBtn:focus {
	background: #555;
}

p {
	line-height: 1.5;
	font-family:Verdana, Geneva, Tahoma, sans-serif;
	font-size: 1.2em;
	color: grey;
	margin: 10px;
}

